.nav {
  padding-top: 38px;

  @include onTablet {
    padding-block: 46px 128px;
  }

  &--language {
    @include onTablet {
      padding-block: 46px 15px;
    }
  }

  &__list {
    list-style: none;
  }

  &__link {
    display: flex;
    align-items: center;
    height: 49px;

    @include contentPaddingInLine;

    color: $secondary-color;
    font-size: 21px;
    font-weight: 500;
    line-height: 130%;
    text-decoration: none;

    &:hover {
      background-color: #110e25;
      color: $third-color;
    }

    @include onTablet {
      padding-left: 155px;
    }
  }
}
